import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { useFetch } from '../../hooks/useFetch';
import { searchPlaces } from '../../services/api';
import PlaceCard from '../../components/Cards/PlaceCard/PlaceCard';
import SearchBar from '../../components/SearchBar/SearchBar';
import { motion } from 'framer-motion';
import styles from './Search.module.scss';

const Search = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const query = searchParams.get('q') || '';
  const category = searchParams.get('category') || '';
  
  const [searchQuery, setSearchQuery] = useState(query);
  const [filters, setFilters] = useState({
    country: '',
    sortBy: 'popularity'
  });

  const { data: places, loading, error } = useFetch(
    () => {
      if (!query && !category) return Promise.resolve([]);
      return searchPlaces(query || category, '', 20);
    },
    [query, category]
  );

  const handleSearch = (newQuery) => {
    setSearchQuery(newQuery);
    setSearchParams({ q: newQuery });
  };

  const handleFilterChange = (filterName, value) => {
    setFilters(prev => ({
      ...prev,
      [filterName]: value
    }));
  };

  const filteredPlaces = places ? places.filter(place => {
    if (filters.country && place.address?.country !== filters.country) {
      return false;
    }
    return true;
  }).sort((a, b) => {
    if (filters.sortBy === 'popularity') {
      return (b.rate || 0) - (a.rate || 0);
    }
    return 0;
  }) : [];

  const countries = [...new Set(places?.map(p => p.address?.country).filter(Boolean) || [])];

  return (
    <div className={styles.search}>
      <div className={styles.searchHeader}>
        <h1 className={styles.title}> </h1>
        <SearchBar onSearch={handleSearch} placeholder=" , , ..." />
      </div>

      {query && (
        <div className={styles.filters}>
          <div className={styles.filterGroup}>
            <label htmlFor="country">:</label>
            <select
              id="country"
              value={filters.country}
              onChange={(e) => handleFilterChange('country', e.target.value)}
            >
              <option value=""> </option>
              {countries.map(country => (
                <option key={country} value={country}>{country}</option>
              ))}
            </select>
          </div>

          <div className={styles.filterGroup}>
            <label htmlFor="sortBy">:</label>
            <select
              id="sortBy"
              value={filters.sortBy}
              onChange={(e) => handleFilterChange('sortBy', e.target.value)}
            >
              <option value="popularity"> </option>
              <option value="name"> </option>
            </select>
          </div>
        </div>
      )}

      <div className={styles.results}>
        {loading ? (
          <div className={styles.loading}>
            <div className={styles.spinner}></div>
             ...
          </div>
        ) : error ? (
          <div className={styles.error}>
            <p>: {error}</p>
            <p className={styles.errorHint}>
                     
            </p>
          </div>
        ) : filteredPlaces.length > 0 ? (
          <>
            <p className={styles.resultsCount}>
               : {filteredPlaces.length}
            </p>
            <div className={styles.placesGrid}>
              {filteredPlaces.map((place, index) => (
                <motion.div
                  key={place.xid || index}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3, delay: index * 0.05 }}
                >
                  <PlaceCard place={place} />
                </motion.div>
              ))}
            </div>
          </>
        ) : query ? (
          <div className={styles.empty}>
            <p>     "{query}"</p>
            <p className={styles.emptyHint}>
                    
            </p>
          </div>
        ) : (
          <div className={styles.empty}>
            <p>   </p>
            <p className={styles.emptyHint}>
              : "Paris", "Eiffel Tower", "London"
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Search;

